/* Search page */
.page-search-template {
    margin-top: 0;
    margin-bottom: 60px; 
}
.search-empty {
    > a {
        display: inline-block;
        margin-top: 20px;
    }
    .search-form {
        max-width: 580px;
        margin: 20px auto 0;
        display: flex;
        width: 100%;
        .input-group {
            width: 100%;
        }
        input {
            flex: 1;  
            height: 40px;
            border-radius: var(--input-radius) 0 0 var(--input-radius);
        }
        .btn {
            flex: 0 0 50px;
            width: 50px;
            height: 40px; 
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 0;
            border-radius: 0 var(--input-radius) var(--input-radius) 0; 
            svg {
                width: 20px;
                height: 20px; 
                path {
                    fill: currentColor;
                }
            }
        }
    }
}
/* End search page */